<template>
  <div class="canvas-wrapper">
    <div class="edit-wrapper">
      <div class="left-column">
        <div class="canvas-wrapper">
          <canvas ref="bottomPreviewCanvas" class="preview-canvas"></canvas>
        </div>
      </div>
      <div class="right-column">
        <div class="top-controls">
          <el-button link @click="undo" :disabled="history.length <= 1 || historyIndex === 0"></el-button>
          <el-button link @click="redo" :disabled="historyIndex === history.length - 1"></el-button>
          <el-button link @click="toggleEraser">{{
            eraserActive ? '' : ''
          }}</el-button>
          <el-button link @click="toggleRepair">{{
            repairActive ? '' : ''
          }}</el-button>
          <el-slider v-model="brushSize" :min="5" :max="50" :step="1" :show-tooltip="true"
            :format-tooltip="formatTooltip" class="brush-size-slider"></el-slider>
        </div>
        <div class="canvas-wrapper">
          <canvas ref="bottomCanvas" class="canvas-layer"></canvas>
          <canvas ref="topCanvas" class="canvas-layer"></canvas>
        </div>
        <div class="bottom-controls">
          <el-button link @click="previousImage"></el-button>
          <el-button link @click="downloadImage"></el-button>
          <el-button link @click="copyToClipboard"></el-button>
          <el-button link @click="handleSubmit"></el-button>
          <el-button link @click="nextImage"></el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, computed, reactive, watch, onBeforeUnmount } from 'vue' //  onBeforeUnmount
import { ElMessage, ElMessageBox } from 'element-plus'
import { getImageDetail, postImageUpdate } from '@service/task'
import UPNG from 'upng-js'

export default {
  name: 'CanvasComponent',
  props: {
    imageId: {
      type: [String, Number],
      required: true,
      default: ''
    },
    imageList: {
      type: Array,
      required: true
    }
  },
  setup(props, { emit }) {
    const baseUrl = import.meta.env.VITE_API_BASE_URL
    const bottomPreviewCanvas = ref(null)
    const previewCtx = ref(null)
    const bottomCanvas = ref(null)
    const topCanvas = ref(null)
    const bottomCtx = ref(null)
    const topCtx = ref(null)
    const eraserActive = ref(false)
    const repairActive = ref(false)
    const history = ref([])
    const historyIndex = ref(-1)
    const brushSize = ref(20)
    const isDrawing = ref(false)
    const originalTopImageData = ref(null)
    const currentId = computed(() => props.imageId)
    const origin = reactive({})
    const mask = reactive({})
    const formatTooltip = (val) => `${val}px`
    const hasModifications = ref(false)
    const originMask = ref(null)
    const topImgRatio = ref({ w: 1, h: 1 })

    const getImageDetailFunc = async () => {
      const startTime = performance.now()
      try {
        const { data } = await getImageDetail(currentId.value)
        origin.value = data.origin
        mask.value = data.mask
        const endTime = performance.now()
        console.log(` - : ${Math.round(endTime - startTime)}ms`)
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      }
    }

    // 
    const copyToClipboard = async () => {
      try {
        await handleSubmit() // 
        const imageUrl = `${baseUrl}/image/${currentId.value}/download` // URL

        // 
        window.focus()
        
        const image = new Image()
        image.crossOrigin = 'Anonymous' // 
        
        await new Promise((resolve, reject) => {
          image.onload = resolve
          image.onerror = reject
          image.src = imageUrl
        })

        const canvas = document.createElement('canvas')
        canvas.width = image.width
        canvas.height = image.height
        const ctx = canvas.getContext('2d')
        ctx.drawImage(image, 0, 0)

        try {
          // 
          canvas.toBlob(async (blob) => {
            if (blob) {
              try {
                // 
                const permission = await navigator.permissions.query({ name: 'clipboard-write' })
                if (permission.state === 'granted' || permission.state === 'prompt') {
                  await navigator.clipboard.write([new ClipboardItem({ 'image/png': blob })])
                  ElMessage.success('')
                } else {
                  throw new Error('')
                }
              } catch (err) {
                console.error(':', err)
                // 
                const url = URL.createObjectURL(blob)
                const link = document.createElement('a')
                link.href = url
                link.download = `image_${currentId.value}.png`
                document.body.appendChild(link)
                link.click()
                document.body.removeChild(link)
                URL.revokeObjectURL(url)
                ElMessage.warning('')
              }
            }
          }, 'image/png')
        } catch (err) {
          throw new Error('')
        }
      } catch (error) {
        console.error('/:', error)
        ElMessage.error('')
      }
    }

    // 
    const calcResizeTarget = (origin_w, origin_h, target_w, target_h) => {
      if (origin_w / origin_h > target_w / target_h) {
        const w = target_w
        const h = Math.round((origin_h * target_w) / origin_w)
        return { width: w, height: h }
      } else {
        const w = Math.round((origin_w * target_h) / origin_h)
        const h = target_h
        return { width: w, height: h }
      }
    }

    const initCanvas = () => {
      const startTime = performance.now()
      //  .canvas-wrapper 
      const canvasWrappers = document.querySelectorAll('.edit-wrapper .canvas-wrapper')

      canvasWrappers.forEach((wrapper, index) => {
        const wrapperWidth = wrapper.clientWidth
        const wrapperHeight = wrapper.clientHeight

        const { width, height } = calcResizeTarget(
          origin.value.width,
          origin.value.height,
          wrapperWidth,
          wrapperHeight
        )

        //  canvas  within the wrapper
        const canvases = wrapper.querySelectorAll('canvas')

        canvases.forEach((canvas) => {
          //  Canvas 
          canvas.width = origin.value.width
          canvas.height = origin.value.height

          //  CSS 
          canvas.style.width = `${width}px`
          canvas.style.height = `${height}px`
        })

        //  Canvas 
        if (index === 0) {
          //  Canvas
          const previewCanvasElem = wrapper.querySelector('.preview-canvas')
          if (previewCanvasElem) {
            previewCtx.value = previewCanvasElem.getContext('2d', { willReadFrequently: true })
          }
        } else if (index === 1) {
          //  Canvas
          const canvasLayers = wrapper.querySelectorAll('.canvas-layer')
          if (canvasLayers.length === 2) {
            bottomCtx.value = canvasLayers[0].getContext('2d', { willReadFrequently: true })
            topCtx.value = canvasLayers[1].getContext('2d', { willReadFrequently: true })
          }
        }
      })

      addDrawingListeners()
      const endTime = performance.now()
      console.log(` - Canvas: ${Math.round(endTime - startTime)}ms`)
    }

    const loadImages = () => {
      const startLoadTime = performance.now()
      
      // Promise
      const loadImagePromise = (src) => {
        return new Promise((resolve, reject) => {
          const img = new Image()
          img.onload = () => resolve(img)
          img.onerror = reject
          img.src = src
        })
      }

      // 
      Promise.all([
        loadImagePromise(`${baseUrl}/${origin.value.file_url}`),
        loadImagePromise(`${baseUrl}/${mask?.value?.file_url}`)
      ]).then(([bottomImg, topImg]) => {
        const imageDataStartTime = performance.now()
        origin.value.width = bottomImg.width
        origin.value.height = bottomImg.height

        initCanvas()

        if (!bottomCtx.value || !previewCtx.value) {
          console.error('Canvas ')
          ElMessage.error('Canvas ')
          return
        }

        //  requestAnimationFrame 
        requestAnimationFrame(() => {
          const drawImageProp = (ctx, img, x, y, w, h) => {
            const imgRatio = img.width / img.height
            const canvasRatio = w / h
            let drawWidth, drawHeight, drawX, drawY

            if (imgRatio > canvasRatio) {
              drawWidth = w
              drawHeight = Math.round(w / imgRatio)
              drawX = x
              drawY = y + (h - drawHeight) / 2
            } else {
              drawHeight = h
              drawWidth = Math.round(h * imgRatio)
              drawY = y
              drawX = x + (w - drawWidth) / 2
            }

            ctx.clearRect(0, 0, w, h)
            ctx.drawImage(img, drawX, drawY, drawWidth, drawHeight)
          }

          // 
          drawImageProp(
            bottomCtx.value,
            bottomImg,
            0,
            0,
            bottomCanvas.value.width,
            bottomCanvas.value.height
          )
          drawImageProp(
            previewCtx.value,
            bottomImg,
            0,
            0,
            bottomPreviewCanvas.value.width,
            bottomPreviewCanvas.value.height
          )

          const imageDataEndTime = performance.now()
          console.log(` - : ${Math.round(imageDataEndTime - imageDataStartTime)}ms`)

          // 
          topImgRatio.value = {
            w: topCanvas.value.width / topImg.width,
            h: topCanvas.value.height / topImg.height
          }

          if (topCanvas.value.width === 0 || topCanvas.value.height === 0) {
            console.error('Canvas ')
            ElMessage.error('Canvas ')
            return
          }

          // Canvasmask
          const offscreenCanvas = new OffscreenCanvas(topCanvas.value.width, topCanvas.value.height)
          const offscreenCtx = offscreenCanvas.getContext('2d')
          offscreenCtx.drawImage(topImg, 0, 0, topCanvas.value.width, topCanvas.value.height)
          
          // CanvasCanvas
          topCtx.value.drawImage(offscreenCanvas, 0, 0)

          try {
            originalTopImageData.value = offscreenCtx.getImageData(
              0,
              0,
              topCanvas.value.width,
              topCanvas.value.height
            )
            saveState()
          } catch (error) {
            console.error(':', error)
            ElMessage.error('')
          }

          const totalLoadTime = performance.now()
          console.log(` - : ${Math.round(totalLoadTime - startLoadTime)}ms`)
        })

        // mask
        if (mask?.value?.file_url) {
          fetch(`${baseUrl}/${mask.value.file_url}`)
            .then((response) => response.blob())
            .then((blob) => blob.arrayBuffer())
            .then((buffer) => {
              const maskStartTime = performance.now()
              const decoded = UPNG.decode(buffer)
              const arrayBuffer = UPNG.toRGBA8(decoded)[0]
              //  TypedArray 
              originMask.value = new Uint8ClampedArray(arrayBuffer)
              const maskEndTime = performance.now()
              console.log(` - mask: ${Math.round(maskEndTime - maskStartTime)}ms`)
            })
        }
      }).catch(error => {
        console.error(':', error)
        ElMessage.error('')
      })
    }

    const addDrawingListeners = () => {
      topCanvas.value.addEventListener('mousedown', startDrawing)
      topCanvas.value.addEventListener('mousemove', draw)
      topCanvas.value.addEventListener('mouseup', stopDrawing)
      topCanvas.value.addEventListener('mouseout', stopDrawing)
    }

    const getMousePos = (e) => {
      const rect = topCanvas.value.getBoundingClientRect()
      const scaleX = topCanvas.value.width / rect.width
      const scaleY = topCanvas.value.height / rect.height
      return {
        x: (e.clientX - rect.left) * scaleX,
        y: (e.clientY - rect.top) * scaleY
      }
    }

    const startDrawing = (e) => {
      if (!eraserActive.value && !repairActive.value) return
      isDrawing.value = true
      draw(e)
    }

    const draw = (e) => {
      if (!isDrawing.value) return
      const pos = getMousePos(e)

      if (!draw.frameRequest) {
        draw.frameRequest = requestAnimationFrame(() => {
          let left = Math.round(pos.x) - Math.floor(brushSize.value / 2)
          let top = Math.round(pos.y) - Math.floor(brushSize.value / 2)
          let width = brushSize.value
          let height = brushSize.value

          // 
          if (left < 0) {
            width += left
            left = 0
          }
          if (top < 0) {
            height += top
            top = 0
          }
          if (left + width > topCanvas.value.width) {
            width = topCanvas.value.width - left
          }
          if (top + height > topCanvas.value.height) {
            height = topCanvas.value.height - top
          }

          // 1
          width = Math.max(width, 1)
          height = Math.max(height, 1)

          if (eraserActive.value || repairActive.value) {
            const imageData = topCtx.value.getImageData(left, top, width, height)
            const data = imageData.data
            const maskData = originMask.value
            const canvasWidth = origin.value.width

            //  TypedArray 
            const view = new DataView(data.buffer)

            for (let y = 0; y < height; y++) {
              const maskY = top + y
              if (maskY >= origin.value.height || maskY < 0) continue

              for (let x = 0; x < width; x++) {
                const maskX = left + x
                if (maskX >= origin.value.width || maskX < 0) continue

                const maskIndex = (maskY * canvasWidth + maskX) * 4
                const index = (y * width + x) * 4

                if (eraserActive.value) {
                  view.setUint32(index, view.getUint32(index) & 0x00FFFFFF, true)
                } else if (repairActive.value) {
                  data[index] = maskData[maskIndex]
                  data[index + 1] = maskData[maskIndex + 1]
                  data[index + 2] = maskData[maskIndex + 2]
                  data[index + 3] = 255
                }
              }
            }

            topCtx.value.putImageData(imageData, left, top)
            hasModifications.value = true
          }

          draw.frameRequest = null
        })
      }
    }

    const debounce = (fn, delay) => {
      let timer = null
      return function (...args) {
        if (timer) clearTimeout(timer)
        timer = setTimeout(() => fn.apply(this, args), delay)
      }
    }

    // 
    const debouncedSaveState = debounce(() => {
      if (isDrawing.value) {
        saveState()
      }
    }, 300)

    const stopDrawing = () => {
      if (isDrawing.value) {
        isDrawing.value = false
        debouncedSaveState()
      }
    }

    const saveState = () => {
      historyIndex.value++
      history.value = history.value.slice(0, historyIndex.value)
      history.value.push(
        topCtx.value.getImageData(0, 0, topCanvas.value.width, topCanvas.value.height)
      )
    }

    const loadState = (index) => {
      if (index < 0 || index >= history.value.length) return
      topCtx.value.putImageData(history.value[index], 0, 0)
    }

    const undo = () => {
      if (historyIndex.value > 0) {
        historyIndex.value--
        loadState(historyIndex.value)
      }
    }

    const redo = () => {
      if (historyIndex.value < history.value.length - 1) {
        historyIndex.value++
        loadState(historyIndex.value)
      }
    }

    const toggleEraser = () => {
      eraserActive.value = !eraserActive.value
      if (eraserActive.value) repairActive.value = false
    }

    const toggleRepair = () => {
      repairActive.value = !repairActive.value
      if (repairActive.value) eraserActive.value = false
    }

    const downloadImage = async () => {
      try {
        await handleSubmit() // 
        window.open(`${baseUrl}/image/${currentId.value}/download`) // 
      } catch {
        ElMessage.error('') //  error 
      }
    }

    const closeEditor = () => {
      if (hasUnsavedChanges.value) {
        ElMessageBox.confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            discardChanges()
            eraserActive.value = false // 
            repairActive.value = false // 
            emit('close')
          })
          .catch(() => {
            // 
          })
      } else {
        eraserActive.value = false // 
        repairActive.value = false // 
        emit('close')
      }
    }

    const discardChanges = () => {
      loadState(0)
      historyIndex.value = 0
      history.value = history.value.slice(0, 1)
    }

    const hasUnsavedChanges = computed(() => {
      return historyIndex.value > 0
    })

    const saveChangesIfNeeded = async () => {
      if (hasModifications.value) {
        await handleSubmit()
      }
    }

    const previousImage = async () => {
      if (currentIndex.value > 0) {
        await saveChangesIfNeeded()
        hasModifications.value = false // 
        emit('changeImage', props.imageList[currentIndex.value - 1].id)
      } else {
        ElMessage.warning('')
      }
    }

    const nextImage = async () => {
      if (currentIndex.value < props.imageList.length - 1) {
        await saveChangesIfNeeded()
        hasModifications.value = false // 
        emit('changeImage', props.imageList[currentIndex.value + 1].id)
      } else {
        ElMessage.warning('')
      }
    }

    const handleSubmit = async () => {
      const submitStartTime = performance.now()
      if (!hasModifications.value) {
        return
      }

      try {
        //  Canvas 
        const canvas = document.createElement('canvas')
        canvas.width = origin.value.width
        canvas.height = origin.value.height
        const ctx = canvas.getContext('2d')

        //  Canvas 
        if (!ctx) {
          throw new Error(' Canvas ')
        }

        // 
        const scaleFactor = Math.min(
          origin.value.width / topCanvas.value.width,
          origin.value.height / topCanvas.value.height
        )
        const scaledWidth = topCanvas.value.width * scaleFactor
        const scaledHeight = topCanvas.value.height * scaleFactor

        // 
        const dx = (canvas.width - scaledWidth) / 2
        const dy = (canvas.height - scaledHeight) / 2

        //  topCanvas  Canvas 
        ctx.drawImage(
          topCanvas.value,
          0,
          0,
          topCanvas.value.width,
          topCanvas.value.height,
          dx,
          dy,
          scaledWidth,
          scaledHeight
        )

        //  Canvas  Blob
        const blob = await new Promise((resolve) => canvas.toBlob(resolve, 'image/png'))

        if (!blob) {
          throw new Error(' Blob')
        }

        //  FormData 
        const formData = new FormData()
        formData.append('file', blob, 'edited_image.png')
        formData.append('new_width', canvas.width.toString())
        formData.append('new_height', canvas.height.toString())

        //  postImageUpdate 
        const response = await postImageUpdate({ file: formData, image_id: currentId.value })
        const submitEndTime = performance.now()
        console.log(` - : ${Math.round(submitEndTime - submitStartTime)}ms`)

        if (response.code === 0) {
          ElMessage.success('')
          // 
          historyIndex.value = 0
          history.value = [
            topCtx.value.getImageData(0, 0, topCanvas.value.width, topCanvas.value.height)
          ]
          hasModifications.value = false // 
        } else {
          throw new Error(response.message || '')
        }
      } catch (error) {
        console.error(':', error)
        ElMessage.error(error.message || '')
        throw error // 
      }
    }

    const currentIndex = computed(() => {
      return props.imageList.findIndex((item) => item.id === props.imageId)
    })

    onMounted(async () => {
      await getImageDetailFunc()
      await loadImages()
      window.addEventListener('resize', initCanvas)
    })

    onBeforeUnmount(() => {
      window.removeEventListener('resize', initCanvas)
    })

    watch(
      () => props.imageId,
      async () => {
        await getImageDetailFunc()
        await loadImages()
        hasModifications.value = false // 
        history.value = [] // 
        historyIndex.value = -1 // 
      }
    )

    return {
      bottomPreviewCanvas,
      bottomCanvas,
      topCanvas,
      eraserActive,
      repairActive,
      undo,
      redo,
      toggleEraser,
      toggleRepair,
      downloadImage,
      copyToClipboard,
      brushSize,
      formatTooltip,
      history,
      historyIndex,
      closeEditor,
      origin,
      mask,
      handleSubmit,
      previousImage,
      nextImage,
      hasUnsavedChanges,
      hasModifications
    }
  }
}
</script>

<style scoped>
.edit-wrapper {
  display: flex;
  width: 100%;
  height: 80vh;
}

.left-column,
.right-column {
  padding: 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  /*  */
  height: 100%;
  /*  */
}

.preview-canvas {
  width: 100%;
  margin-top: 40px;
  height: 100%;
  /*  100%  */
  border: 1px solid #ccc;
  flex-grow: 1;
  /*  canvas  */
  box-sizing: border-box;
}

.canvas-wrapper {
  position: relative;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;
  /*  */
}

.preview-canvas,
.canvas-layer {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}

.top-controls,
.bottom-controls {
  height: 40px;
  padding: 5px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.brush-size-slider {
  width: 200px;
  margin: 0 20px;
}

button {
  margin: 0 5px;
}

.brush-size-slider {
  width: 200px;
  margin: 0 20px;
}

.left-column,
.right-column {
  padding: 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  height: 100%;
}
</style>

